.desc {
  margin: 4px 0;
}

.title {
  display: inline-flex;
  align-items: center;
}

.projMark {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 20px;
  padding: 4px 9px;
  margin: 2px;
  margin-left: 8px;
  border-radius: 10px;
  border: var(--tag-border);
  font-size: 14px;
  font-weight: normal;
}

.projMarkTag {
  font-size: 14px;
  font-weight: normal;
}

.projMarkTag::before {
  content: '|';
  margin: 0 8px;
}

.tagsContainer {
  font-size: 16px;
  font-family: monospace;
  // font-weight: bold;
  padding: 4px 0;

  span {
    margin-right: 8px;
  }

  span::before {
    content: '#';
  }
}

.achievement {
  > ul {
    list-style: square inside;

    > li {
      margin: 8px 0;
    }
  }
}
