@import '../resume/style/var';

.container {
  width: 100%;
  background-color: #f7f8f9;

  // skin theme
  // page
  // --page-background-color: #fff;
  #{$page-background-color}: #fff;
  --main-font-color: #000;
  --section-line-color: #a0a0a0;

  // timeline
  --timeline-color: #81d4fa;
  --timeline-milestone-color: #ffffff;
  --timeline-item-hover-color: #0000aa10;
  // --timeline-data-color

  // tag
  --tag-border: 1px solid #495057;
  --tag-background-color: #767e8680;
  --tag-font-color: #000;
  --tag-font-color-on-background: #fff;
}

@media screen and (prefers-color-scheme: dark) {
  .container {
    background-color: #22303f;
    // skin theme
    // page
    // --page-background-color: #18222d;
    #{$page-background-color}: #18222d;
    --main-font-color: #d1d5d9;
    --section-line-color: #a0a0a0;

    // timeline
    --timeline-color: #9caec7;
    --timeline-milestone-color: #ffffff;
    --timeline-item-hover-color: #303075f3;
    --timeline-item-hover-color: #293544;
    // --timeline-data-color

    // tag
    --tag-border: 1px solid #9caec7;
    --tag-background-color: #393f4e;
    --tag-font-color: #9caec7;
    --tag-font-color-on-background: #9caec7;
  }
}

.page {
  width: 100%;

  display: flex;
  flex-direction: column;
  padding: 16px;

  background-color: var($page-background-color);

  font-size: 16px;
  color: var(--main-font-color);
  line-height: 1.15;

  z-index: 0;

  h2 {
    font-size: 20px;
  }

  h3 {
    font-size: 18px;
    // line-height: 1.1;
    margin: 6px 0;
  }

  section > hgroup {
    margin: 5px 0 10px;
    padding: 4px 0;
    border-bottom: 1px solid var(--section-line-color, #000);
    // position: sticky;
    // background-color: var(--page-background-color);
    // z-index: 1;
    // top: 100px;
  }
}

@media screen and (min-width: $screen-layout-breakpoint) {
  .container {
    padding: 20px 0;
  }

  .page {
    max-width: $screen-layout-breakpoint;
    margin: 0 auto;
    border-radius: 4px;
    box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.1);
  }
}

.alert {
  margin: 0 auto;
  align-self: center;
  text-align: center;
}
