:root {
  --body-width: auto;
  --body-height: auto;
}

body {
  width: var(--body-width);
  height: var(--body-height);
  margin: 0;
  font-family: PingFangSC-Light, 'PingFang SC', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Microsoft YaHei', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  background-color: #f7f8f9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@page {
  /* margin: 5mm; */
  size: A4 portrait;
  margin-top: 5mm;
  margin-right: 5mm;
  margin-left: 5mm;
  widows: 3;
  orphans: 3;
}

@media print {
  :root {
    --body-width: 200mm;
    --body-height: 297mm;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    break-after: avoid;
  }
  img {
    break-inside: avoid;
    break-after: avoid;
  }
  table,
  pre {
    break-inside: avoid;
  }
  /* ul,
  ol,
  dl {
    break-before: avoid;
    break-after: avoid;
  } */
}
