@import '../style/var';

.profile {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 16px;
  line-height: 1;
}

.part {
  // width: 320px;
  // min-width: 320px;
  width: 100%;
  max-width: 375px;
  padding: 8px;
}

.name {
  font-size: 30px;
  font-weight: bold;
}

.jobTitle {
  margin: 8px 0 0;
}

@media screen and (min-width: $screen-layout-breakpoint) {
  .profile {
    position: sticky;
    top: 0;
    background-color: var(--page-background-color);
    z-index: 2;
  }
}

@media screen and (max-width: $screen-layout-breakpoint) {
  .profile {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.contactItem {
  height: 20px;
  margin: 4px 0;
  display: flex;
  align-items: center;
  font-family: monospace;

  a {
    color: var(--main-font-color);
    text-decoration: none;
  }

  a:hover {
    text-decoration: underline;
  }
}

.contactIcon {
  width: 16px;
  height: 16px;
  margin-right: 4px;
}
