.cell {
  // margin: 0;
  padding: 4px;

  a {
    color: var(--main-font-color);
    text-decoration: none;
  }

  a:hover {
    text-decoration: underline;
  }
}

.desc {
  margin: 5px 0;
}

.tagContainer {
  div {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    padding: 2px 10px;
    border-radius: 10px;
    margin: 4px 0;
    background-color: var(--tag-background-color, #fff);

    span {
      color: var(--tag-font-color-on-background, #000);
    }
  }

  div:nth-of-type(n + 2) {
    margin-left: 4px;
  }
}
