@import '../style/var';

$line_width: 2px; // point_border_width
$point_radius: 6px;

.timeSide {
  display: none;
  flex-direction: column-reverse;
  justify-content: flex-end;
  align-items: center;
  min-width: 80px;
  padding: 10px 0;

  > span {
    display: block;
  }

  margin: 4px;
  font-size: 14px;
  font-family: monospace;
  font-weight: bold;
}

.timeInRow {
  display: block;
  > span {
    display: inline-flex;
    justify-content: center;
    align-items: center;
  }
}

.item {
  display: flex;
  flex-direction: row;
}

.itemContentBox {
  display: flex;
  flex: 1;
  padding: 4px 0;
}

.itemContent {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 8px 0;
}

// desktop
@media screen and (min-width: $screen-layout-breakpoint) {
  .timeSide {
    display: flex;
  }

  .timeInRow {
    display: none;
  }

  .itemContentBox {
    border-left: $line_width solid var(--timeline-color);
    position: relative;
    margin-left: 10px;
    padding: 4px;
    padding-left: 10px;
  }

  .itemContentBox::before {
    content: '';
    box-sizing: border-box;
    width: $point_radius * 2;
    height: $point_radius * 2;
    border-radius: $point_radius * 2;
    background-color: var(--timeline-milestone-color, #fff);
    position: absolute;
    // top: 16px;
    left: -$line_width / 2 - $point_radius;
    // border: $line_width solid $line_color;
    border: $line_width solid var(--timeline-color);
    top: 16px + $point_radius;
  }

  .itemContent {
    position: relative;
    margin-left: 10px;
    padding: 8px;
  }

  .itemContent:hover {
    background-color: var(--timeline-item-hover-color);
    border-radius: 8px;
  }

  .itemContent:hover::before {
    content: '';
    box-sizing: content-box;
    height: 0;
    width: 0;
    border: $point_radius solid transparent;
    border-top-color: var(--timeline-item-hover-color);
    border-right-color: var(--timeline-item-hover-color);
    position: absolute;
    left: -$point_radius * 2;
    top: 10px + $point_radius * 2;
  }
}
