@import '../style/var';

.box {
  margin: 4px 0;
}

.subTitle {
  display: none;
}

.tagContainer {
  width: 100%;
  font-family: monospace;
  color: var(--tag-font-color, #000);
}

.tag {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 2px 6px 2px 0;
  text-align: right;
}

.emptyPoint {
  display: inline-block;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  border: 1px solid var(--main-font-color);
  margin: 0 2px;
}

.fullPoint {
  display: inline-block;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: var(--main-font-color);
  margin: 0 2px;
}

@media screen {
  @supports ((display: grid) and (grid-template-columns: repeat(4, 1fr))) {
    .tagContainer {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
    }

    @media (max-width: $screen-layout-breakpoint) {
      .tagContainer {
        grid-template-columns: repeat(2, 1fr);
      }
    }

    .tag {
      justify-content: flex-end;
    }
  }
}

@media print {
  .tag > span::before {
    content: '#';
  }

  .emptyPoint {
    display: none;
  }

  .fullPoint {
    display: none;
  }
}
